@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600&display=swap");

.banner {
  background: url("./Assets/banner-2.jpg") no-repeat center / cover;
  width: 100%;
}

.quote {
  width: 100%;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 32px;
  font-weight: 600;
  color: white;
  display: grid;
  padding-top: 1.5%;
  padding-bottom: 1.5%;
}

#Contact {
  background-color: #eef1f5;
  display: grid;
  font-size: 22px;
  width: 100%;
  justify-content: center;
  padding-top: 4%;
  padding-bottom: 5%;
}

/* .form {
  display: grid;
} */

.form-intro {
  width: 96%;
  padding: 2%;
}

.form-div {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  align-self: center;
  justify-self: center;
}

.actual-form {
  width: 50%;
  background-color: #454547;
  color: white;
  display: grid;
  /* grid-template-columns: repeat(auto-fit, minmax(100px, 1fr)); */
  align-self: center;
  justify-self: center;
  padding: 5%;
}

.labels {
  padding: 2%;
}

.input {
  padding: 2%;
  resize: vertical;
}

.Contact-Button {
  font-size: 24px;
  color: white;
  text-decoration: none;
  background: #8fb3e5;
  border: none;
  justify-self: center;
  cursor: pointer;
  align-self: center;
  transition: 1s;
  width: 100%;
  padding: 2%;
  margin-top: 8%;
}

.Contact-Button:hover {
  background: #454547;
}
